//Миксин - возможность делать шаблоны кода и переиспользовать его. Возможно добавлять аргументы.
@mixin style-link($color) {
    display: inline-block;
    text-transform: uppercase;
    text-decoration: none;
    color: $color; }

@mixin clearfix {
    &::after {
        content: "";
        display: table;
        clear: both; } }

@mixin phone {
    @media (max-width: 600px) {
        @content; } }

/* В аргумент миксина мы будем указывать несколько вариантов брейкпоинтов, таких как:
    - phone
    -tab-port
    -tab-land
    -big-screen */


@mixin respond($breakpoint) {
    @if $breakpoint == phone {
        @media (max-width: 600px) {
            @content; } }

    @if $breakpoint == tab-port {
        @media (max-width: 900px) {
            @content; } }

    @if $breakpoint == tab-land {
        @media (max-width: 1240px) {
            @content; } }

    @if $breakpoint == big-screen {
        @media (min-width: 1800px) {
            @content; } } }










